import '../styles/styles.scss'

import grass_asset from "./assets/grass.png";
import sand_asset from "./assets/sand.png";
import shallowWater_asset from "./assets/shallow_water.png";
import deepWater_asset from './assets/deep_water.png'
import spruce_asset from "./assets/spruce.png";



function Terrain(name, asset, passable, movecost) {
  this.name = name
  this.asset = asset
  this.passable = passable
  this.movecost = movecost
}

export const terrains_map = {
  '.': new Terrain('', grass_asset, true, 10),
  's': new Terrain('', sand_asset, true, 20),
  '~': new Terrain('', shallowWater_asset, true, 30),
  '=': new Terrain('', deepWater_asset, true, 60),
  'w': new Terrain('', spruce_asset, false, 9999),
};

const terNames_map = {
  '': '.',
  '': 's',
  '': '~',
  '': '=',
  '': 'w',
}



export default function Info(props){
    return (
      <div className="info">
        {['.', 's', '~', '=', 'w'].map(item => {
          const curTerrain = terrains_map[item]
          let passable = curTerrain.passable

          const handleClick = () => {
            props.setMode('TERRAIN')
            props.setStart(null)
            props.setFinish(null)
            props.setPath([])
            props.setChosenTerrain(terNames_map[curTerrain.name])
          }

          return <div className='terrain_info_block' key={item} onClick={() => handleClick()}>
            <img
              className='terrain_img'
              src={curTerrain.asset}
              alt={''}
            />
            <div className="terrain_info">
              <h3>{curTerrain.name}</h3>
              {!passable ? 
                <p><span className='terrain_info-span'></span></p> :
                <p><span className='terrain_info-span'>:</span> {curTerrain.movecost ? curTerrain.movecost : ''}</p>}
            </div>
          </div>
        })}
        <div className='start_finish_blocks'> 
          <Btn setMode={props.setMode} mode={'START'} text={''} setStart={props.setStart} setFinish={props.setFinish} setPath={props.setPath}/>
          <Btn setMode={props.setMode} mode={'FINISH'} text={''} setStart={props.setStart} setFinish={props.setFinish} setPath={props.setPath}/>
        </div>
        <div className='distance_block'>: <span>{props.distance ? props.distance : ' '}</span></div>
      </div>
    );
}

const Btn = ({setMode, mode, text, setStart, setFinish, setPath}) => {
  const handleClick = () => {
    setMode(mode)
    if (mode === 'START') setStart(null)
    if (mode === 'FINISH') setFinish(null)
    setPath([])
  }

  return (
    <div className='btn' onClick={() => handleClick()}>
      {text}
    </div>
  );
};